
.summary__para {
    margin: 0 15px;
    text-align: justify;
    font-size: 14px;
    color: #fff;
}
.wrap {
    padding-bottom: 100px;
    overflow: hidden;
    min-height: 100vh;
    box-sizing: border-box;
}
body {
    font: 300 12px/1.5 sans-serif;
    color: grey;
    background-color: #fff
}
.mod_focus {
    position: relative;
    overflow: hidden
}
.focus__body {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3
}
.focus__info {
    display: -webkit-box;
    -webkit-box-align: center;
    margin: 0 15px 25px
}
.focus__text {
    -webkit-box-flex: 1
}
.focus__desc,.focus__tit {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 14px
}
.focus__tit {
    position: relative;
    height: 36px;
    overflow: hidden;
    font-size: 24px
}
.focus__tit_text {
    position: absolute;
    white-space: nowrap
}
.focus__bg {
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover
}
.focus__bg::after {
    content: "";
    display: block;
    padding-top: 100%
}
.focus__gradient {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    background-image: -webkit-linear-gradient(top,transparent,currentColor 80%)
}
.focus__gradient::after {
    content: "";
    display: block;
    padding-top: 60%
}
.focus__gap {
    margin-right: 10px
}
.focus__play {
    position: relative;
    display: block;
    width: 42px;
    height: 42px;
    margin-left: 10px;
    border: solid 2px;
    border-radius: 42px
}
.focus__play::before {
    content: "";
    position: absolute;
    top: 12px;
    left: 16px;
    height: 0;
    width: 0;
    line-height: 999em;
    overflow: hidden;
    border-color: transparent transparent transparent currentColor;
    border-width: 9px 15px;
    border-style: solid;
    border-radius: 2px
}
.focus__play--pause::before {
    left: 14px;
    width: 5px;
    height: 18px;
    border-width: 0;
    background-color: currentColor
}
.focus__play--pause::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 24px;
    width: 5px;
    height: 18px;
    border-radius: 2px;
    background-color: currentColor
}
.focus__play_text {
    opacity: 0
}
.mod_tit {
    position: relative;
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-align: center;
    margin: 25px 0;
    font-size: 18px
}
.tit__more {
    position: absolute;
    top: -6px;
    right: 10px;
    width: 40px;
    height: 40px;
    line-height: 999em;
    overflow: hidden
}
.tit__more::before {
    content: "";
    position: absolute;
    top: 17px;
    left: 15px;
    width: 7px;
    height: 7px;
    border-style: solid;
    border-width: 0 1px 1px 0;
    -webkit-transform: rotate(-45deg)
}
.tit__more::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 12px;
    width: 16px;
    height: 16px;
    border-style: solid;
    border-width: 1px;
    border-radius: 999px
}
.mod_loading {
    position: relative;
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-align: center;
    height: 55px
}
.loading__text {
    margin-left: 5px;
    font-size: 12px;
    color: grey
}
.loading__icon {
    display: block;
    width: 20px;
    height: 20px;
    background: url(/static/icon_loading.png) no-repeat;
    background-size: cover;
    -webkit-animation: ani_loading 1s infinite
}
.mod_complete {
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-align: center;
    height: 55px;
    font-size: 14px
}
.mod_loading_fixed {
    position: fixed;
    left: 0;
    top: 50%;
    width: 100%;
    margin-top: -26px;
    z-index: 100
}
.mod_loading_full {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(0,0,0,.5)
}
.mod_loading_full .loading__text {
    color: rgba(255,255,255,.6)
}
.mod_prompt {
    padding-top: 100px;
    text-align: center
}
.mod_prompt__img {
    display: block;
    margin: 0 auto 40px
}
.mod_prompt__tit {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 400
}
.mod_prompt__desc {
    margin: 0 50px;
    line-height: 14px
}
.c_txt1 {
    color: #fff
}
.c_txt2 {
    color: rgba(255,255,255,.6)
}
.c_bor1,.c_bor1::after {
    border-color: rgba(255,255,255,.15)
}
.c_ico1 {
    color: #fff
}



